.main-container {
  background-color: var(--white);
  height: 420px;
}

.blue-container {
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  background-color: var(--primary);
  height: 280px;
  position: relative;
}

.weather-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  margin-left: 25px;
  margin-top: 20px;
  cursor: pointer;
}

.arrow {
  color: var(--white);
}

.arrow:hover {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transform: translateX(-8px);
  transform: translateX(-8px);
}

.new-search {
  color: var(--white);
  margin-left: 10px;
  font-weight: bold;
  font-size: 1.2rem;
}

.new-search:hover {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transform: translateX(-8px);
  transform: translateX(-8px);
}

.date {
  text-align: center;
  color: var(--white);
  font-size: 1.1rem;
  margin-top: 10px;
}

.current-weather {
  background-color: var(--white);
  width: 90%;
  margin: 20px auto;
  border-radius: 20px;
  -webkit-box-shadow: 0px 25px 14px 1px rgba(122, 122, 122, 0.6);
  -moz-box-shadow: 0px 25px 14px 1px rgba(122, 122, 122, 0.6);
  box-shadow: 0px 25px 14px 1px rgba(122, 122, 122, 0.6);
}

.top {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}

.bottom {
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: black;
  padding: 5px 15px 30px 15px;
}

.city {
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 1;
  letter-spacing: 1px;
  text-align: left;
  margin: 0;
  color: black;
}

.temperature {
  font-weight: 600;
  font-size: 3.7rem;
  width: auto;
  letter-spacing: -5px;
  margin: 2px 0;
  text-align: left;
  color: black;
}

.weather-description {
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1;
  margin: 0;
  color: black;
  text-align: left;
}

.weather-icon {
  width: 150px;
}

.parameter-info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.parameter-value {
  font-weight: 600;
  font-size: 1.2rem;
  margin-top: 10px;
}

.parameter-label {
  font-weight: 400;
  font-size: 1.1rem;
  margin-top: 2px;
}

/* Tablet devices */
@media (min-width: 576px) {
}

/* Laptop devices */
@media (min-width: 768px) {
  .current-weather {
    width: 80%;
    margin: 20px auto;
  }
}
/* Desktop */
@media (min-width: 992px) {
  .current-weather {
    width: 70%;
  }
}
/* Large devices */
@media (min-width: 1200px) {
  .current-weather {
    width: 60%;
  }
}

/* XXL devices */
@media (min-width: 1400px) {
  .current-weather {
    width: 50%;
  }
  .header {
    margin-left: 200px;
  }
}

/* XXXL devices */
@media (min-width: 2500px) {
  .current-weather {
    width: 30%;

  }
  .header {
    margin-left: 800px;
  }
}